
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100%;
  margin-top: 5%;
}
.wrapper {
  height: 100%;
}

.box-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 400px;
  min-height: 400px;
  border-radius: 5px;
}
.game-wrapper {
  padding: 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  background-color: #fcfcfc;
}
.score {
  text-align: center;
}
.game-info {
  width: 150px;
}
.game-info h2 {
  text-align: center;
  margin: 0;
  color: #222;
}

.box {
  width: 50px;
  height: 50px;
  margin: 5px;
  background-color: #fff;
  -webkit-box-shadow: 0 3px 8px -2px rgba(95, 7, 162, 0.45);
          box-shadow: 0 3px 8px -2px rgba(95, 7, 162, 0.45);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  cursor: pointer;
  font-weight: bolder;
  padding: 3px;
  border-radius: 5px;
  background-color: #5e08a2;
  color: #ffffff;
  border: 1px solid #5e08a2;
}
.box:hover {
  -webkit-box-shadow: 0 6px 8px -2px rgba(95, 7, 162, 0.45);
          box-shadow: 0 6px 8px -2px rgba(95, 7, 162, 0.45);
  -webkit-transition: -webkit-box-shadow 0.5s;
  transition: -webkit-box-shadow 0.5s;
  transition: box-shadow 0.5s;
  transition: box-shadow 0.5s, -webkit-box-shadow 0.5s;
}
.box__disabled {
  background-color: #bbb !important;
  cursor: default;
  border: 1px solid #aaa;
}
.isOpen {
  background-color: #fff;
}
.box img {
  width: inherit;
  height: inherit;
}
.remove {
  display: none;
  -webkit-transition: display 9s;
  transition: display 9s;
}
